<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card>
      <!-- 提醒区域 -->
      <el-alert
        :closable="false"
        title="注意：只允许为第三级分类设置相关参数！"
        type="warning"
        show-icon
      >
      </el-alert>
      <!-- 选择商品分类级联选择框 -->
      <el-row class="cat_opt">
        <el-col>
          <span>选择商品分类:&nbsp;</span>
          <el-cascader
            v-model="selectedKeys"
            :options="cateList"
            @change="cateCascaderChanged"
            :props="cateCascaderProps"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- 标签页 -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <!-- 添加动态参数面板 -->
        <el-tab-pane label="动态参数" name="many">
          <el-button
            type="primary"
            :disabled="selectedKeys.length === 3 ? false : true"
            size="mini"
            @click="showAddParamsDialog"
            >添加参数</el-button
          >
          <!-- 动态参数表格 -->
          <el-table :data="manyTableData" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand">
              <template v-slot="props">
                <!-- <pre>{{ props.row }}</pre> -->
                <!-- 参数的属性标签 -->
                <el-tag
                  :key="i"
                  v-for="(tag,i) in props.row.attr_vals"
                  closable
                  :disable-transitions="false"
                  @close="handleAttrTagClose(props.row,i)"
                >
                  {{ tag }}
                </el-tag>
                <!-- 添加参数属性的输入框 -->
                <el-input
                  class="input-new-tag"
                  v-if="props.row.inputVisible"
                  v-model="props.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleAddAttrInputConfirm(props.row)"
                  @blur="handleAddAttrInputConfirm(props.row)"
                >
                </el-input>
                <!-- 添加属性的按钮 -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showAddAttrInput(props.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <!-- 索引行 -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column
              prop="attr_name"
              label="参数名称"
            ></el-table-column>
            <el-table-column label="操作">
              <template v-slot="props">
                <el-button type="primary" class="el-icon-edit" size="mini"
                @click="showEditDialog(props.row)"
                  >编辑</el-button
                >
                <el-button type="danger" class="el-icon-delete" size="mini"
                @click="deleteParams(props.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!-- 添加静态参数面板 -->
        <el-tab-pane label="静态属性" name="only"
          ><el-button
            type="primary"
            :disabled="selectedKeys.length === 3 ? false : true"
            size="mini"
            @click="showAddParamsDialog"
            >添加参数</el-button
          >

          <!-- 静态参数表格 -->
          <el-table :data="onlyTableData" border stripe>
            <!-- 展开行 -->
            <el-table-column type="expand">
              <template v-slot="props">
                <!-- <pre>{{ props.row }}</pre> -->
                <!-- 参数的属性标签 -->
                <el-tag
                  :key="i"
                  v-for="(tag,i) in props.row.attr_vals"
                  closable
                  :disable-transitions="false"
                  @close="handleAttrTagClose(props.row,i)"
                >
                  {{ tag }}
                </el-tag>
                <!-- 添加参数属性的输入框 -->
                <el-input
                  class="input-new-tag"
                  v-if="props.row.inputVisible"
                  v-model="props.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleAddAttrInputConfirm(props.row)"
                  @blur="handleAddAttrInputConfirm(props.row)"
                >
                </el-input>
                <!-- 添加属性的按钮 -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showAddAttrInput(props.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <!-- 索引行 -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column
              prop="attr_name"
              label="参数名称"
            ></el-table-column>
            <el-table-column label="操作">
              <template v-slot="props">
                <el-button type="primary" class="el-icon-edit" size="mini"
                @click="showEditDialog(props.row)"
                  >编辑</el-button
                >
                <el-button type="danger" class="el-icon-delete" size="mini"
                @click="deleteParams(props.row.attr_id)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!-- 添加参数对话框 -->
    <el-dialog
      :title="'添加'+titleText"
      :visible.sync="addParamsDialogVisible"
      width="50%"
      @close="addParamsDialogClosed"
    >
      <el-form ref="addParamsFormRef" :model="addParamsForm" label-width="80px" :rules="addParamsFormRules">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addParamsDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addParams"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 修改参数对话框 -->
    <el-dialog
      :title="'修改'+titleText"
      :visible.sync="editParamsDialogVisible"
      width="50%"
      @close="editParamsDialogClosed"
    >
      <el-form ref="editParamsFormRef" :model="editParamsForm" label-width="80px" :rules="editParamsFormRules">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editParamsDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editParams"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: '',
      // 所有商品分类列表
      cateList: [],
      // 商品分类级联选择器props配置选项,指定级联选择器所需字段对应的数据源字段
      cateCascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      // seleted商品分类的id数组
      selectedKeys: [],
      // 动态参数列表
      manyTableData: [],
      // 静态参数列表
      onlyTableData: [],
      // 激活的标签页
      activeName: 'many',
      // 控制添加参数属性的输入框显示隐藏
      addAttrInputVisible: false,
      // 添加参数属性的值
      addAttrInputValue: '',
      // 控制添加参数对话框显示和隐藏
      addParamsDialogVisible: false,
      // 控制修改参数对话框显示和隐藏
      editParamsDialogVisible: false,
      // 添加参数的表单数据
      addParamsForm: {
        attr_id: 0,
        attr_name: ''
      },
      // 修改参数的表单数据
      editParamsForm: {
        attr_name: ''
      },
      // 添加参数的验证规则
      addParamsFormRules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      },
      // 修改参数的验证规则
      editParamsFormRules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    // 当前选中分类的id
    cateId () {
      if (this.selectedKeys.length === 3) {
        return this.selectedKeys[2]
      }
      return null
    },
    // 当前添加属性对话框的title
    titleText () {
      if (this.activeName === 'many') {
        return '动态参数'
      } else {
        return '静态属性'
      }
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 监听商品分类选择框selectChange,请求分类列表数据
    async cateCascaderChanged () {
      this.getParamsList()
    },
    // 获取所有商品分类列表
    async getCateList () {
      const { data: res } = await this.$http.get('categories', { type: 3 })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败!')
      }
      this.$message.success('获取商品列表成功!')
      this.cateList = res.data
      //   console.log(res.data);
    },
    // 获取所有的参数params列表
    async getParamsList () {
      // 如果不是3级参数,则重置selectedKeys数组
      if (this.selectedKeys.length !== 3) {
        this.selectedKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      // 是3级参数,获取参数列表数据
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('获取参数列表失败!')
      }
      this.$message.success('获取参数列表成功!')
      // attr_vals分割成数组
      res.data.forEach((item, index) => {
        item.attr_vals = item.attr_vals.trim().length ? item.attr_vals.split(',') : []
        item.inputVisible = false
        item.inputValue = ''
      })
      //   console.log(res.data);
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else if (this.activeName === 'only') {
        this.onlyTableData = res.data
      }
    },
    // tab 标签点击事件处理函数
    handleTabClick () {
      this.getParamsList()
      //   console.log(this.activeName);
    },
    // 修改分类参数属性
    async setAttr_vals (row) {
      const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: this.activeName,
        attr_vals: row.attr_vals.join(',')
      })
      if (res.meta.status !== 200) {
        return this.$message.error('更新参数属性失败')
      }
      this.$message.success('更新参数属性成功!')
    },
    // 监听tag属性标签关闭按钮
    handleAttrTagClose (row, index) {
      row.attr_vals.splice(index, 1)
      this.setAttr_vals(row)
    },
    // 点击按钮,显示添加参数属性input文本输入框
    showAddAttrInput (row) {
      row.inputVisible = true
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 监听添加参数属性文本输入框的keyup.enter和blur的原生事件
    async handleAddAttrInputConfirm (row) {
      const inputValue = row.inputValue
      // 判断文本输入框的值是否有效
      if (inputValue.trim().length) {
        // 有效值,发起修改请求
        row.attr_vals.push(inputValue)
        this.setAttr_vals(row)
        // console.log(row);
      }

      // 隐藏文本输入框,显示+ New tag标签,并重置文本输入框的值
      row.inputVisible = false
      row.inputValue = ''
    },
    // 显示添加参数params对话框
    showAddParamsDialog () {
      this.addParamsDialogVisible = true
    },
    // 添加参数对话框关闭
    addParamsDialogClosed () {
      // 重置表单
      this.$refs.addParamsFormRef.resetFields()
      // 重置组件内和表单绑定的数据
      this.addParamsForm.attr_name = ''
    },
    // 发起添加参数的请求
    addParams () {
      // 验证表单
      this.$refs.addParamsFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        console.dir(this.addParamsForm)
        // 验证通过,发起添加请求
        const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
          attr_name: this.addParamsForm.attr_name,
          attr_sel: this.activeName,
          attr_vals: ''
        })
        if (res.meta.status !== 201) {
          return this.$message.error('添加参数失败!')
        }
        this.$message.success('添加参数成功!')
        // console.log(res.data)
        this.addParamsForm.attr_name = ''
        this.addParamsDialogVisible = false
        this.getParamsList()
      })
    },
    // 控制修改参数的对话框的显示隐藏
    showEditDialog (params) {
      this.editParamsForm.attr_id = params.attr_id
      this.editParamsForm.attr_name = params.attr_name
      this.editParamsDialogVisible = true
    },
    // 添加参数对话框关闭
    editParamsDialogClosed () {
      // 重置表单
      this.$refs.editParamsFormRef.resetFields()
      // 重置组件内和表单绑定的数据
      this.editParamsForm.attr_name = ''
      this.editParamsForm.attr_id = 0
    },
    // 发起修改参数的请求
    editParams () {
      this.$refs.editParamsFormRef.validate(async valid => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.put(`categories/${this.cateId}/attributes/${this.editParamsForm.attr_id}`, {
          attr_name: this.editParamsForm.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 200) {
          return this.$message.error('更新参数失败!')
        }
        this.$message.success('更新参数成功!')
        this.editParamsDialogVisible = false
        this.getParamsList()
      })
    },
    // 删除参数
    async deleteParams (attrId) {
      const confirmResult = await this.$confirm('此操作将永久删除该参数, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除!')
      }

      const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${attrId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除参数失败!')
      }
      this.$message.success('删除参数成功!')
      this.getParamsList()
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
  display: flex;
  align-items: center;
}
.el-tag + .el-tag {
  margin-left: 10px;
  margin-bottom: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: top;
}
</style>
