<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }">商品列表</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片区域 -->
    <el-card>
      <el-alert
        title="添加商品信息"
        type="info"
        show-icon
        center
        :closable="false"
      ></el-alert>

      <!-- 步骤条 -->
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
      >
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- 表单内嵌套tab -->
      <el-form
        ref="addFormRef"
        :model="addForm"
        :rules="addFormRules"
        label-width="80px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          tab-position="left"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <!-- 标签页 添加商品信息的每个步骤 -->
          <!-- 基本信息 -->
          <el-tab-pane name="0" label="基本信息">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="cateCascaderProps"
                @change="cateChanged"
                clearable
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!-- 商品参数 -->
          <el-tab-pane name="1" label="商品参数">
            <el-form-item
              v-for="item in manyTableData"
              :label="item.attr_name"
              :key="item.attr_id"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  :label="attr_val"
                  v-for="(attr_val, i) in item.attr_vals"
                  :key="i"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!-- 商品属性 -->
          <el-tab-pane name="2" label="商品属性">
            <el-form-item
              :label="item.attr_name"
              v-for="item in onlyTableData"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <!-- 商品图片 -->
          <el-tab-pane name="3" label="商品图片">
            <!-- 图片上传组件 -->
            <el-upload
              class="upload-demo"
              :action="actionUrl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headersObj"
              :on-success="picUploaded"
            >
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <!-- 商品内容 -->
          <el-tab-pane name="4" label="商品内容">
            <!-- 富文本编辑器 -->
            <quill-editor
              v-model="addForm.goods_introduce"
            />
            <el-button type="primary" class="btnAdd" @click="addGoods">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!-- 预览图片对话框 -->
    <el-dialog
      title="预览图片"
      :visible.sync="previewDialogVisible"
      width="50%"
    >
      <img :src="previewPath" alt="" />
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  data () {
    return {
      activeIndex: '0',
      // 添加商品的表单数据
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        // 商品的分类数组
        goods_cat: [],
        // 图片数组
        pics: [],
        // 商品的详情介绍
        goods_introduce: '',
        // 商品的参数（数组），包含 `动态参数` 和 `静态属性`
        attrs: []
      },
      // 表单验证规则对象
      addFormRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '请选择商品分类', trigger: 'blur' }
        ]
      },
      // 商品列表数据
      cateList: [],
      // 商品分类级联选择器配置
      cateCascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 动态参数数据
      manyTableData: [],
      // 静态参数数据
      onlyTableData: [],
      // 图片上传地址
      actionUrl: 'http://www.ysqorz.top:8888/api/private/v1/upload',
      // 图片上传请求头
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 图片真实路径
      previewPath: '',
      // 控制预览图片对话框的显示和隐藏
      previewDialogVisible: false
    }
  },
  computed: {
    cateId () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 获取商品分类信息
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类列表失败!')
      }
      this.$message.success('获取商品分类列表成功!')
      this.cateList = res.data
      // console.log(res.data)
    },
    // 商品分类级联选择器选中项改变触发
    cateChanged () {
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // 切换标签页时触发
    beforeTabLeave (activeName, oldActiveName) {
      // 未选择商品分类名,不允许切换标签页
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('请选择商品分类')
        return false
      }
    },
    // 点击标签页触发
    async tabClicked () {
      // 判断当前激活tab,如果是商品参数页,则请求商品动态参数
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'many'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取商品参数失败!')
        }
        // 转化成数组
        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.trim().length === 0 ? [] : item.attr_vals.split(',')
        })
        // console.log(res.data)
        this.manyTableData = res.data
        this.$message.success('获取商品参数成功!')
      } else if (this.activeIndex === '2') {
        // 如果激活的是商品属性,发起静态属性请求
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: {
              sel: 'only'
            }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取商品参数失败!')
        }
        this.onlyTableData = res.data
        console.log(res.data)
      }
    },
    // 图片上传成功
    picUploaded (res) {
      const pic = { pic: res.data.tmp_path }

      this.addForm.pics.push(pic)
      // console.log(this.addForm.pics)
    },
    // 预览图片时触发
    handlePreview (file) {
      console.log(file)
      const url = file.response.data.url
      this.previewPath = url
      this.previewDialogVisible = true
    },
    // 移除上传图片时触发
    handleRemove (picInfo) {
      // 获取要删除的图片临时路径
      const pic = picInfo.response.data.tmp_path
      // 找到对应图片数组的索引
      const i = this.addForm.pics.findIndex((x) => x.pic === pic)
      // 从图片数组里删除
      this.addForm.pics.splice(i, 1)
      // console.log(this.addForm.pics)
    },
    // 添加商品
    addGoods () {
      // console.log(this.addForm)
      // 表单验证
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('请填写必要的表单项')
        }
        // 验证通过
        // 深拷贝addForm
        const form = _.cloneDeep(this.addForm)
        // 按照api参数要求格式化数据
        form.goods_cat = form.goods_cat.join(',')
        this.manyTableData.forEach(item => {
          const newObj = { attr_id: item.attr_id, attr_value: item.attr_vals.join(',') }
          form.attrs.push(newObj)
        })
        // console.log(this.manyTableData)
        this.onlyTableData.forEach(item => {
          const newObj = { attr_id: item.attr_id, attr_vals: item.attr_vals }
          form.attrs.push(newObj)
        })
        // console.log(this.onlyTableData)
        // console.log(form)
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) {
          return this.$message.error('添加商品失败!')
        }
        this.$message.success('添加商品成功!')
        this.$router.push('/goods')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-steps {
  margin-top: 15px;
  margin-bottom: 15px;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}

.btnAdd{
    margin-top: 20px;
}
</style>
