<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 添加分类按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
            >添加分类</el-button
          >
        </el-col>
      </el-row>

      <!-- 商品分类列表 -->
      <tree-table
        :data="goodsList"
        :columns="colunms"
        border
        :selection-type="false"
        :expand-type="false"
        :show-index="true"
        index-text="#"
        :show-row-hover="true"
      >
        <!-- 是否有效 -->
        <template v-slot:cat_deleted="props">
          <!-- <pre>
                {{props.row}}
            </pre> -->
          <i
            v-if="!props.row.cat_deleted"
            class="el-icon-success"
            style="color: lightgreen"
          ></i>
          <i
            v-if="props.row.cat_deleted"
            style="color: lightgreen"
            class="el-icon-error"
          ></i>
        </template>
        <!-- 分类等级 -->
        <template v-slot:cat_level="props">
          <!-- <pre>
                {{props.row}}
            </pre> -->
          <el-tag v-if="props.row.cat_level === 0" type="primary">一级</el-tag>
          <el-tag v-if="props.row.cat_level === 1" type="success">二级</el-tag>
          <el-tag v-if="props.row.cat_level === 2" type="warning">三级</el-tag>
        </template>
        <!-- 操作列 -->
        <template v-slot:operation>
          <!-- <pre>
                {{props.row}}
            </pre> -->
          <el-button type="primary" icon="el-icon-edit" size="mini"
            >编辑</el-button
          >
          <el-button type="danger" icon="el-icon-setting" size="mini"
            >删除</el-button
          >
        </template>
      </tree-table>

      <!-- 分页组件 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>

      <!-- 添加分类对话框 -->
      <el-dialog
        title="添加分类"
        :visible.sync="addCateDialogVisible"
        width="50%"
        @close="addCateDialogClosed"
      >
        <!-- 添加分类的表单 -->
        <el-form
          :model="addCateForm"
          :rules="addCateFormRules"
          ref="addCateFormRef"
          label-width="100px"
        >
          <el-form-item label="分类名称" prop="cat_name">
            <el-input v-model="addCateForm.cat_name"></el-input>
          </el-form-item>
          <!-- options 指定配置数据源 -->
          <!-- props 指定配置选项 -->
          <el-form-item label="父级分类">
            <el-cascader
              v-model="selectedKeys"
              :options="parentCateList"
              @change="parentCateChanged"
              clearable
              :props="cascaderProps"
            ></el-cascader>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="addCateDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="addCate">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 商品列表
      goodsList: [],
      // 请求参数查询对象
      queryInfo: {
        type: 3,
        pagesize: 5,
        pagenum: 1
      },
      // 总数据
      total: 0,
      // tree-table的列定义
      colunms: [
        {
          label: '分类名称',
          prop: 'cat_name',
          width: '200px'
        },
        {
          label: '是否有效',
          // 将当前列定义为模板列
          type: 'template',
          // 自定义模板列名称
          template: 'cat_deleted'
        },
        {
          label: '排序',
          type: 'template',
          template: 'cat_level'
        },
        {
          label: '操作',
          type: 'template',
          template: 'operation',
          width: '200px'
        }
      ],
      // 控制添加分类对话框的显示和隐藏
      addCateDialogVisible: false,
      // 添加分类表单数据
      addCateForm: {
        // 父级分类id
        cat_pid: 0,
        // 分类名称
        cat_name: '',
        // 分类层级,默认1级分类
        cat_level: 0
      },
      // 添加分类表单的验证规则
      addCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 所有父级分类
      parentCateList: [],
      // 级联选择器配置选项
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        checkStrictly: true
      },
      // 选中的父级分类id
      selectedKeys: []
    }
  },
  created () {
    // 获取商品列表
    this.getGoodsList()
  },
  methods: {
    // 获取商品列表
    async getGoodsList () {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败')
      }
      this.$message.success('获取商品列表成功')
      this.goodsList = res.data.result
      this.total = res.data.total
      //   console.log(res);
    },
    // 监听 pagesize 改变事件
    handleSizeChange (pagesize) {
      this.queryInfo.pagesize = pagesize
      this.getGoodsList()
    },
    // 监听 页码 变化事件
    handleCurrentChange (pagenum) {
      this.queryInfo.pagenum = pagenum
      this.getGoodsList()
    },
    // 显示添加分类对话框
    async showAddCateDialog () {
      // 获取所有父级分类的数据
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取父级分类失败')
      }

      this.parentCateList = res.data
      //   console.log(res)
      // 显示对话框
      this.addCateDialogVisible = true
      this.$message.success('获取父级分类成功')
    },
    // 监听父级分类改变
    parentCateChanged () {
      // 根据selectedKeys的长度来判断添加的是几级分类和父级id
      switch (this.selectedKeys.length) {
        // 1级分类,
        case 0:
          this.addCateForm.cat_pid = 0
          this.addCateForm.cat_level = 0
          break
          // 2级分类
        case 1:
          this.addCateForm.cat_pid = this.selectedKeys[0]
          this.addCateForm.cat_level = 1
          break
          // 3级分类
        case 2:
          this.addCateForm.cat_pid = this.selectedKeys[1]
          this.addCateForm.cat_level = 2
          break
      }
      //  console.dir(this.addCateForm)
    },
    // 发起请求添加分类
    addCate () {
    //   发起请求前先验证表单
      this.$refs.addCateFormRef.validate(async (valid) => {
        if (!valid) {
          return
        }
        // 验证通过,发起请求
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
          return this.$message.error('添加分类失败')
        }
        this.$message.success('添加表单成功')
        // 重置添加表单绑定数据
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_name = ''
        this.addCateForm.cat_level = 0
        this.selectedKeys = []
        // 隐藏对话框
        this.addCateDialogVisible = false
        // 重新获取商品分类数据
        this.getGoodsList()
      })
    //   console.log(this.addCateForm);
    },
    // 监听添加分类对话框关闭,清空表单和重置数据
    addCateDialogClosed () {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    }
  }
}
</script>

<style lang="less" scoped>
.zk-table {
  margin-top: 15px;
}
.el-cascader{
    width: 100%;
}

</style>
